        charset: "utf8", // 字符集
        username: "", // 名称
        password: "", // 密码
        dbPath: "", // sqlite 文件地址
        // model 包
        model: {
          name: "model",
          fields: ["json", "form", "gorm"],
        },
        // service
        service: {
          interName: "service",
          relName: "impl",
          methods: [],
          params: "",
        },
        // dao
        dao: {
          interName: "dao",
          relName: "impl",
          methods: [],
          params: "",
        },
      },

      modelCheckList: [],
      addForm: null,
      formRules: {
        name: [{ required: true, message: "不能为空", trigger: "blur" }],
        host: [{ required: true, message: "不能为空", trigger: "blur" }],
        port: [{ required: true, message: "不能为空", trigger: "blur" }],
        dbname: [{ required: true, message: "不能为空", trigger: "blur" }],
